html {
  font-size: 10px;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.5rem;
  background-color: #ebe7de;
}

p {
  color: #000;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 5rem;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.titulo {
  color: #5e311e;
  text-transform: uppercase;
  text-align: center;
  font-size: 3.2rem;
  line-height: 1.5;
}
.titulo::after {
  content: "";
  background-image: url(../img/cafe.svg);
  display: block;
  width: 12rem;
  height: 10rem;
  margin: 0 auto;
}
.titulo span {
  color: #c39c63;
  font-size: 2rem;
  display: block;
}

.header {
  padding: 5rem 0;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .header {
    min-height: 70rem;
  }
}
.header::before {
  content: "";
  position: absolute;
  background-color: #000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.6;
}
.header .contenido-header {
  position: relative;
}
.header .contenido-header .texto-header {
  font-size: 3.8rem;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  font-weight: 700;
  line-height: 1.3;
}

.header-inicio {
  background-image: url(../img/header_inicio.jpg);
}

.header-nosotros {
  background-image: url(../img/header_nosotros.jpg);
}

.header-proceso {
  background-image: url(../img/header_proceso.jpg);
}

.header-menu {
  background-image: url(../img/header_menu.jpg);
}

.header-galeria {
  background-image: url(../img/header_galeria.jpg);
}

.header-contacto {
  background-image: url(../img/header_contacto.jpg);
}

.nav-principal {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .nav-principal {
    margin-top: unset;
    flex-direction: row;
  }
}

.link {
  color: #fff;
  font-size: 2rem;
  font-weight: 700;
  text-transform: uppercase;
}
.link:hover {
  color: #c39c63;
}

.barra {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10rem;
  }
}
.barra .logo {
  width: 55%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .barra .logo {
    width: 20rem;
    margin: unset;
  }
}

.contenido-principal {
  background-color: #fff;
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .contenido-principal {
    margin-top: -10rem;
    position: relative;
    padding: 5rem 10rem;
  }
}
.contenido-principal .texto {
  line-height: 1.8;
  text-align: center;
}

.contenedor-iconos {
  margin: 4rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .contenedor-iconos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 5rem;
  }
}
.contenedor-iconos .iconos {
  width: 12rem;
  margin: 0 auto;
}
.contenedor-iconos .icono-texto {
  text-align: center;
  font-size: 3rem;
  color: #5e311e;
  font-family: "Bona Nova", sans-serif;
}

.galeria {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 1000px) {
  .galeria {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 2rem;
  }
}
@media (min-width: 1000px) {
  .galeria .imagen:nth-child(1) {
    grid-row: 1/3;
  }
  .galeria .imagen:nth-child(1) img {
    height: 62rem;
  }
  .galeria .imagen:nth-child(2) {
    grid-column: 2/4;
  }
  .galeria .imagen:nth-child(2) img {
    width: 40rem;
  }
  .galeria .imagen:nth-child(4) {
    grid-row: 2/4;
  }
  .galeria .imagen:nth-child(4) img {
    height: 62rem;
  }
  .galeria .imagen:nth-child(5) {
    grid-column: 3/5;
  }
  .galeria .imagen:nth-child(5) img {
    width: 40rem;
  }
  .galeria .imagen:nth-child(6) {
    grid-column: 3/5;
  }
  .galeria .imagen:nth-child(6) img {
    width: 40rem;
  }
  .galeria .imagen:nth-child(7) {
    grid-row: 3/5;
  }
  .galeria .imagen:nth-child(7) img {
    height: 62rem;
  }
  .galeria .imagen:nth-child(8) {
    grid-column: 2/5;
  }
  .galeria .imagen:nth-child(8) img {
    width: 62rem;
  }
  .galeria .imagen:nth-child(9) {
    grid-column: 1/3;
  }
  .galeria .imagen:nth-child(9) img {
    width: 40rem;
  }
  .galeria .imagen:nth-child(10) {
    grid-column: 3/5;
  }
  .galeria .imagen:nth-child(10) img {
    width: 40rem;
  }
}
.galeria .imagen img {
  width: 30rem;
  height: 30rem;
  object-fit: cover;
}

.nuestro-menu {
  background-color: #5e311e;
  padding-bottom: 3rem;
}
.nuestro-menu::before, .nuestro-menu::after {
  content: "";
  background-image: url(../img/menu_arriba.svg);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: 75rem;
  height: 18rem;
  display: block;
  position: relative;
  top: 6rem;
}
.nuestro-menu::after {
  background-image: url(../img/menu_abajo.svg);
  top: -7rem;
}
.nuestro-menu .titulo-principal {
  color: #fff;
  text-align: center;
  font-size: 4rem;
  text-transform: uppercase;
  line-height: 1.2;
}
.nuestro-menu .titulo-principal span {
  font-size: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .nuestro-menu .menu-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
  }
}
.nuestro-menu .menu {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.nuestro-menu .menu .titulo-menu {
  color: #fff;
  text-transform: uppercase;
}
.nuestro-menu .menu .box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 1.5;
  padding: 0 2rem;
  margin-bottom: 2rem;
}
.nuestro-menu .menu .box .texto-menu {
  color: #c39c63;
  font-size: 2.2rem;
  margin: 0;
}
.nuestro-menu .menu .box span {
  display: block;
  color: #fff;
}
.nuestro-menu .menu .box .precio {
  color: #fff;
  font-size: 4rem;
  font-weight: 700;
  margin: 0;
}

.testimoniales {
  margin-top: -10rem;
  padding: 5rem 2rem 8rem;
  background-color: #fff;
}
.testimoniales .box-testimonial {
  background-color: #ebe7de;
  border-radius: 1rem;
  padding: 1.5rem 2.5rem;
  max-width: 45rem;
  margin: 0 auto;
  line-height: 2;
}
.testimoniales .box-testimonial .autor-testimonial {
  font-family: "Bona Nova", sans-serif;
  text-align: right;
  font-size: 2.2rem;
  color: #5e311e;
}

.footer {
  background-color: #5e311e;
}
.footer .derechos {
  margin: 0;
  padding: 2rem;
  text-align: center;
  color: #fff;
}

.footer-contenedor {
  padding: 5rem 0;
  background-color: #fff;
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  justify-content: center;
  row-gap: 2rem;
}
.footer-contenedor .footer-box {
  text-align: center;
  font-family: "Bona Nova", sans-serif;
}
.footer-contenedor .footer-box .box-titulo {
  text-transform: uppercase;
  color: #c39c63;
  font-size: 2.3rem;
  margin-bottom: 3rem;
}
.footer-contenedor .footer-box p, .footer-contenedor .footer-box a {
  font-size: 1.8rem;
  color: #000;
}

@media (min-width: 768px) {
  .footer-contenedor {
    grid-template-rows: unset;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1000px) {
  .nosotros-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 4rem;
  }
}
.nosotros-grid img {
  height: auto;
  margin: 0 auto;
  border: 2.5rem solid #fff;
  transform: rotate(-6deg);
  box-shadow: 2px 3px 5px 1px rgba(0, 0, 0, 0.2);
  margin-bottom: 5rem;
}
.nosotros-grid .texto-nosotros {
  line-height: 2;
}
.nosotros-grid .texto-nosotros blockquote {
  padding: 0.5rem;
  margin: 0;
  font-size: 2rem;
  border-left: 0.5rem solid #5e311e;
}

.box-proceso {
  margin-bottom: 5rem;
}
.box-proceso:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .box-proceso {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
  .box-proceso:nth-child(even) .procesos {
    order: -1;
  }
  .box-proceso .imagen,
  .box-proceso .procesos {
    flex: 1;
  }
}
.box-proceso .titulo-proceso {
  margin: 1rem 0;
  text-transform: uppercase;
  color: #5e311e;
}
.box-proceso .titulo-proceso::after {
  content: "";
  display: block;
  width: 50%;
  height: 0.3rem;
  background-color: #c39c63;
}
.box-proceso .texto-proceso {
  line-height: 2;
  margin: 0;
}

@media (min-width: 768px) {
  .seccion-menu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
    align-items: end;
  }
}
.seccion-menu p {
  line-height: 2;
  font-size: 1.8rem;
}
.seccion-menu .imagen-seccion-menu {
  background-image: url(../img/cafe.jpg);
  background-repeat: no-repeat;
  background-position: center bottom;
  height: 25rem;
  background-size: 90%;
}
@media (min-width: 768px) {
  .seccion-menu .imagen-seccion-menu {
    background-position: 20% 100%;
    background-size: 100%;
  }
}
@media (min-width: 1000px) {
  .seccion-menu .imagen-seccion-menu {
    background-size: 90%;
    height: 40rem;
  }
}

@media (min-width: 768px) {
  .contenedor-contacto {
    background-image: url(../img/granos_cafe.jpg);
    background-size: 40%;
    background-repeat: no-repeat;
    background-position: center right;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
  }
}

.formulario .campo {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
.formulario .campo label {
  font-weight: 700;
  text-transform: uppercase;
  font-size: 2.2rem;
}
.formulario .campo input:not([type=submit]),
.formulario .campo select {
  padding: 0.5rem;
  border: 0.5rem solid #5e311e;
}
.formulario .campo input[type=submit] {
  border: none;
  background-color: #5e311e;
  padding: 0.5rem;
  color: #fff;
  font-size: 2.2rem;
  font-weight: 700;
}
.formulario .campo input[type=submit]:hover {
  cursor: pointer;
  background-color: #994f30;
  transition: 0.2s ease;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #5e311e;
}

.activo {
  color: #c39c63;
  font-size: 2rem;
  font-weight: 700;
  text-transform: uppercase;
}

.pd-bottom-0 {
  padding-bottom: 0;
}